.Join{
    padding: 0 2rem;
    gap: 10rem;
}

.left-j{
    color: white;
    font-size: 3rem;
    font-weight: bold;
    text-transform: uppercase;
    position: relative;
}

.left-j>hr{
    position: absolute;
    width: 10.5rem;
    border: 2px solid var(--blue);
    border-radius: 20%;
    margin: -15px 0;
}

.left-j>div{
    display: flex;
    gap: 1rem;
}

