.btn-j{
    background-color: #2b6bda88;
    margin-left: 22rem;
    padding: 1rem 2.5rem;
} 

.btn-j:hover{
    background-color: transparent;
    color: white;
    border: 1px solid #2b6bda88;
}

.contact h2{
    margin-bottom: 8rem;
}

.heading{
        color: white;
        font-weight: bold;
        font-size: 2rem;
        margin-left: 40rem;
    }

.contact>form{
    max-width: 50rem;
    margin-left: 20rem;
    text-align: center;
    margin-bottom: 3rem;
}



.contact form .input-box{
    display: flex;
    justify-content:space-between;
    flex-wrap: wrap;
    margin-top: 2rem;
}

.contact form .input-box input,
.contact form textarea{
    width: 100%;
    padding: 1rem;
    font-size: 1rem;
    color: #fff;
    background: #323946;
    /* border-radius: 0.8rem; */
    margin: .7rem 0;
}

.contact form .input-box input{
    width: 49%;
}

.contact form textarea{
    resize: none;
}

.contact form .btn{
    margin-top: 3rem;
    cursor: pointer;
}

@media screen and (max-width: 768px){
    /* .Join{
